import en from '../langs/en'
import zh from '../langs/zh'

function calcLangs(langs) {
  return langs.reduce((pre, lang) => {
    // eslint-disable-next-line no-param-reassign
    pre[lang.langName] = lang
    return pre
  }, {})
}

export default class Lang {
  /**
   * @param {Object} langObj
   * */
  static addLangSource(langObj) {
    if (!langObj.langName) throw new Error('Key `langName` in lang object is missed')
    this.langs[langObj.langName] = langObj
    console.log('Your custom language set successful')
  }

  /**
   * @param {String} key
   * @param {Object} params
   * */
  static $t(key, params = {}) {
    if (!this.langs[this.lang]) throw new Error(`(Lang.js) No language: ${this.lang}`)
    const val = this.langs[this.lang][key]
    if (!val) {
      console.warn(`(Lang.js) Key: ${key} is not exist`)
      return key
    }
    const replaceRules = Object.keys(params).map(k => ({
      rule: new RegExp(`{${k}}`, 'g'),
      value: params[k],
    }))
    return replaceRules.reduce((pre, rule) => pre.replace(rule.reg, rule.value), val)
  }

  /**
   * @param {String} lang
   * */
  static setLang(lang) {
    if (!this.langs[lang]) throw new Error(`(Lang.js) No language: ${lang}`)
    this.lang = lang
  }
}

Lang.langs = calcLangs([en, zh])
Lang.lang = zh.langName
