import MetaScale from '@livelybone/meta-scale'

/**
 * @const
 * @readonly
 * @type {ReadonlyArray}
 * @property {Number} 0, timestamp, millisecond 毫秒
 * @property {Number} 1, open
 * @property {Number} 2, highest
 * @property {Number} 3, lowest
 * @property {Number} 4, close
 * @property {Number} 5, volume
 * @property {Number} 6, amount, optional
 * */
export const PricesArray = Object.freeze([Date.now(), 1, 1, 1, 1, 1, 1])

/**
 * @const
 * @readonly
 * @type {ReadonlyArray}
 * K线类型
 * */
export const CandleTypes = Object.freeze([
  { name: 'candles', value: 'normal' },
  { name: 'heikin-ashi', value: 'average' },
])

/**
 * K线时段
 * */
export const Times = Object.freeze([
  { name: 'time', value: 60 * 1000 },
  { name: '1min', value: 60 * 1000 },
  { name: '5min', value: 5 * 60 * 1000 },
  { name: '15min', value: 15 * 60 * 1000 },
  { name: '30min', value: 30 * 60 * 1000 },
  { name: '1hour', value: 60 * 60 * 1000 },
  { name: '4hour', value: 4 * 60 * 60 * 1000 },
  { name: '1day', value: 24 * 60 * 60 * 1000 },
  { name: '5day', value: 5 * 24 * 60 * 60 * 1000 },
  { name: '1week', value: 7 * 24 * 60 * 60 * 1000 },
  // { name: '1mon', value: 30 * 24 * 60 * 60 * 60000 }, // rough 粗略的
])

/**
 * K线指标
 * */
export const Indicators = Object.freeze({
  base: [
    { name: 'MA', value: 'MA' },
    { name: 'EMA', value: 'EMA' },
    { name: 'BOLL', value: 'BOLL' },
    { name: 'SAR', value: 'SAR' },
  ],
  technical: [
    { name: 'MACD', value: 'MACD' },
    { name: 'KDJ', value: 'KDJ' },
    { name: 'StochRSI', value: 'StochRSI' },
    { name: 'RSI', value: 'RSI' },
    { name: 'DMI', value: 'DMI' },
    { name: 'OBV', value: 'OBV' },
    { name: 'DMA', value: 'DMA' },
    { name: 'TRIX', value: 'TRIX' },
    { name: 'BRAR', value: 'BRAR' },
    { name: 'VR', value: 'VR' },
    { name: 'EMV', value: 'EMV' },
    { name: 'WR', value: 'WR' },
    { name: 'ROC', value: 'ROC' },
    { name: 'MTM', value: 'MTM' },
    { name: 'PSY', value: 'PSY' },
  ],
})

export const PageScale = MetaScale()

/**
 * x 轴单位显示长度基准值
 * */
export const XAxisUnitWidth = 75 / PageScale

/**
 * y 轴单位显示长度基准值
 * */
export const YAxisUnitWidth = 30 / PageScale

/**
 * Months
 * */
export const Months = Object.freeze([
  { name: 'Jan', value: 1 },
  { name: 'Feb', value: 2 },
  { name: 'Mar', value: 3 },
  { name: 'Apr', value: 4 },
  { name: 'May', value: 5 },
  { name: 'Jun', value: 6 },
  { name: 'Jul', value: 7 },
  { name: 'Aug', value: 8 },
  { name: 'Sep', value: 9 },
  { name: 'Oct', value: 10 },
  { name: 'Nov', value: 11 },
  { name: 'Dec', value: 12 },
])

/**
 * Big.js 进位规则
 * */
export const RoundTypes = Object.freeze({
  down: 0,
  halfUp: 1,
  halfDown: 2,
  up: 3,
})

export const Sizes = Object.freeze({
  candleWidth: Math.round(6 / PageScale),
  candleMinWidth: Math.round(1 / PageScale) * 2,
  textDelta: Math.round(8 / PageScale),
  markerWidth: Math.round(100 / PageScale),
  yMarkerHeight: Math.round(20 / PageScale),
  xMarkerHeight: Math.round(24 / PageScale),
  xHeightDelta: Math.round(8 / PageScale),
  defaultFontSize: Math.round(10 / PageScale),
  candleChartPadding: Math.round(10 / PageScale),
  indicatorChartHeight: Math.round(100 / PageScale),
  indicatorChartPadding: Math.round(10 / PageScale),
  volumeChartHeight: Math.round(100 / PageScale),
  volumeChartPaddingBottom: Math.round(5 / PageScale),
  loadingSize: Math.round(64 / PageScale),
})
