import { PageScale } from './ImmutableData'

export function supportSVG() {
  const SVG_NS = 'http://www.w3.org/2000/svg'
  return !!document.createElementNS && !!document.createElementNS(SVG_NS, 'svg').createSVGRect
}

export function getRect(el) {
  // getBBox, getBoundingClientRect, getComputedText
  return el.getBoundingClientRect()
}

export function getComputedStyle(el, pseudoElt = null) {
  return window.getComputedStyle(el, pseudoElt)
}

export function className(prices) {
  const [, open, , , close] = prices
  return close - open > 0 ? 'up' : 'down'
}

export function cPageScale(num) {
  return Math.round(num / PageScale)
}
